.fondo{
    background-color: #E9E9E9;
}
.titulo{
    color: $Naranja;
    text-transform: uppercase;
    margin: 0;
    padding: 3rem 0 0 0;
    text-align: center;
    font-family:'Lucida Sans', sans-serif;
}
.sub-titulo{
    color: #000;
    text-transform: uppercase;
    font-weight: 50;
    text-align: center;
    font-family: 'Lucida Sans Regular', sans-serif;
    margin: 0;
    padding-top: 1rem;
}
.info-inhouse{
    display: block;
    @include Tableta{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }
    padding-top: 4rem;

    .info-tipo{
        background-color: #363232;
        margin-right: 2rem;
        border-radius: 1rem;
        padding: 8rem 2rem 2rem 2rem;
        margin-bottom: 5rem;
        @include Tableta{
            margin-bottom: 0;
        }
        &:last-child{
            @include Tableta{
                margin-right: 0;
            }
        }
        .icono{
            display: inline-block;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            background-color: #5374C2;
            height: 10rem;
            width: 10rem;
            margin: 0 auto;
            i{
                display: block;
                font-size: 2rem;
                color: $Blanco;
            } 
        }

        .titulo-info{
            text-align: center;
            margin: 0;
            color: $Blanco;
            font-family: 'Lucida Sans', sans-serif;
            text-transform: uppercase;
            margin-top: 1rem;
        }
        .info-sub{
            font-family: 'Lucida Sans', sans-serif;
            color: #D9C4C4;
            text-align: justify;
        }
    }
}